import Web3 from "web3";
import { toChecksumAddress } from "ethereumjs-util";
import { recoverTypedSignature_v4 } from "eth-sig-util";
import ContractJson from "../abi/Controller.json";
import detectEthereumProvider from "@metamask/detect-provider";
import Erc20Json from "../abi/Erc20.json";
import Network from "./global";

export const web3 = new Web3(Network.automataAddress);
const contractAddress = Network.contractAddress;
const contract = new web3.eth.Contract(ContractJson.abi, contractAddress);
const axios = require("axios");
const httpUrl = Network.rpcAddress;

async function setMetamaskProvider() {
  if (web3.currentProvider !== window.ethereum) {
    const provider = await detectEthereumProvider();
    if (provider && provider === window.ethereum) {
      web3.setProvider(window.ethereum);
    } else {
      throw new Error("MetaMask not installed");
    }
  }
}

function setAutomataProvider() {
  web3.setProvider(Network.automataAddress);
}

function getResult(result, options) {
  if (result.length === 0) {
    return [];
  } else {
    let maxVote = Math.max(...result);
    if (maxVote === 0) {
      return ["No result"];
    }
    let finalResult = [];
    for (let i = 0; i < options.length; i++) {
      if (result[i] == maxVote) {
        finalResult.push(options[i]);
      }
    }
    return finalResult;
  }
}

export function toFixed(amount, decimal) {
  let balance;
  if (amount == 0) {
    return 0;
  }
  if (decimal > 3) {
    balance = web3.utils
      .toBN(amount)
      .div(web3.utils.toBN(10 ** (decimal - 3)))
      .toNumber();
    balance = (balance / 1000).toFixed(3);
  } else {
    balance = (amount / 10 ** decimal).toFixed(decimal);
  }
  return balance;
}

export async function checkMetaMask() {
  const provider = await detectEthereumProvider();
  return provider === window.ethereum;
}

export async function getCurrentSpace() {
  setAutomataProvider();
  let currentSpace = contract.methods.currentSapce().call();
  return currentSpace;
}

export async function getWorkspaceById(id) {
  setAutomataProvider();
  let workspace = await contract.methods.workspaces(id).call();
  return workspace;
}

export async function getAllWorkSpace() {
  setAutomataProvider();
  let currentspace = await contract.methods.currentSpace().call();
  let bs = currentspace >> 20;
  let allWorkSpaces = [];
  for (let i = 1; i <= bs; i++) {
    let workspace = await contract.methods.workspaces(i << 20).call();
    allWorkSpaces.push(workspace);
  }
  return allWorkSpaces;
}

export async function addWorkSpace(spaceInfo) {
  await setMetamaskProvider();
  const accounts = await window.ethereum.request({
    method: "eth_requestAccounts"
  });
  const account = accounts[0];
  let addWorkSpaceMethod = contract.methods.addWorkspace(
    spaceInfo.contract,
    web3.utils.stringToHex(JSON.stringify(spaceInfo))
  );
  let gas = await addWorkSpaceMethod.estimateGas({ from: account });
  await addWorkSpaceMethod
    .send({
      from: account,
      value: "0x00",
      gasPrice: "21000000000",
      gas: gas * 2
    })
    .on("transactionHash", function(hash) {
      return hash;
    })
    .on("error", function(e) {
      throw e;
    });
}

export async function getProposals(workspaceId) {
  setAutomataProvider();
  await contract.methods.currentSpace().call();
  let workspace = await contract.methods.workspaces(workspaceId << 20).call();
  let bs = (workspace.pros << 12) >> 12;
  let pros = workspace.pros;
  let prosMap = [];
  for (let i = 1; i <= bs; i++) {
    let proposal = await contract.methods.proposals(pros).call();
    pros = pros - 1;
    prosMap.push(proposal);
  }
  return prosMap;
}

export async function getProposalsById(proposalId) {
  setAutomataProvider();
  const proposal = await contract.methods.proposals(proposalId).call();
  return proposal;
}

export async function addProposal(
  workspaceId,
  startTime,
  endTime,
  number,
  info
) {
  await setMetamaskProvider();
  const accounts = await window.ethereum.request({
    method: "eth_requestAccounts"
  });
  const account = accounts[0];
  let addProposalMethod = contract.methods.addProposal(
    workspaceId,
    startTime,
    endTime,
    number,
    web3.utils.stringToHex(JSON.stringify(info))
  );
  const gas = await addProposalMethod.estimateGas({ from: account });
  await addProposalMethod
    .send({
      from: account,
      value: "0x00",
      gasPrice: "21000000000",
      gas: gas * 2
    })
    .on("transactionHash", function(hash) {
      return hash;
    })
    .on("error", e => {
      throw e;
    });
}

export async function subscribeEvent(event, callback) {
  await setMetamaskProvider();
  contract.once(event, callback);
}

export async function callVote(proposalId, option) {
  await setMetamaskProvider();
  const accounts = await window.ethereum.request({
    method: "eth_requestAccounts"
  });
  const accountAddress = accounts[0];
  const chainId = Network.chainInfo.id;
  const domain = [
    { name: "name", type: "string" },
    { name: "version", type: "string" },
    { name: "chainId", type: "uint256" },
    { name: "verifyingContract", type: "address" }
  ];

  const vote = [
    { name: "voter", type: "uint256" },
    { name: "proposal", type: "uint256" },
    { name: "option", type: "uint32" },
    { name: "timestamp", type: "uint64" }
  ];

  const domainData = {
    name: "Witness",
    version: "0.1.0",
    chainId: chainId.toString(),
    verifyingContract: Network.contractAddress
  };

  const message = {
    voter: "0x000000000000000000000000" + accountAddress.substring(2),
    proposal: web3.utils.numberToHex(proposalId),
    option: option,
    timestamp: Math.floor(Date.now() / 1000)
  };

  const msg = JSON.stringify({
    types: {
      EIP712Domain: domain,
      Vote: vote
    },
    domain: domainData,
    primaryType: "Vote",
    message: message
  });

  return web3.currentProvider.send(
    {
      method: "eth_signTypedData_v4",
      params: [accountAddress, msg],
      from: accountAddress
    },
    async function(error, result) {
      if (error) {
        throw new Error(error);
      }
      const signature = result.result.substring(2);
      const r = "0x" + signature.substring(0, 64);
      const s = "0x" + signature.substring(64, 128);
      const v = parseInt(signature.substring(128, 130), 16);
      const recovered = recoverTypedSignature_v4({
        data: JSON.parse(msg),
        sig: result.result
      });
      if (toChecksumAddress(recovered) === toChecksumAddress(accountAddress)) {
        const params = { msg: JSON.parse(msg), v, r, s };
        const data = {
          jsonrpc: "2.0",
          method: "geode_witness_vote",
          params: {
            vote: params
          },
          id: 73
        };
        return await axios.post(httpUrl, data);
      } else {
        throw new Error("Failed to verify signer when comparing");
      }
    }
  );
}

export async function getProposalOptions(proposalId, tokenAddress, options) {
  try {
    web3.setProvider(Network.chainInfo.rpcUrl);
    let erc20 = new web3.eth.Contract(Erc20Json.abi, tokenAddress);
    const decimal = await erc20.methods
      .decimals()
      .call()
      .catch(err => {
        console.debug(err);
        return {};
      });
    setAutomataProvider();
    const results = await contract.methods
      .getProposalOptions(proposalId)
      .call()
      .catch(err => {
        console.debug(err);
        return {};
      });
    if (results) {
      const finalResult = getResult(
        results.map(r => parseInt(r)),
        options
      );
      const result = results.map(r =>
        typeof decimal === "string" ? toFixed(r, decimal) : parseInt(r)
      );
      return { finalResult, result };
    } else {
      return {};
    }
  } catch (err) {
    console.debug(err);
    return {};
  }
}

export async function getBlockNumber() {
  web3.setProvider(Network.chainInfo.rpcUrl);
  return await web3.eth.getBlockNumber();
}

export async function getBalance(tokenAddress) {
  await setMetamaskProvider();
  const accounts = await window.ethereum.request({ method: "eth_accounts" });
  const account = accounts[0];
  if (!account) {
    return "";
  }
  let chainId = await window.ethereum.request({ method: "eth_chainId" });
  if (parseInt(chainId) !== Network.chainInfo.id) {
    web3.setProvider(Network.chainInfo.rpcUrl);
  }
  let contract = new web3.eth.Contract(Erc20Json.abi, tokenAddress);
  try {
    const amount = await contract.methods.balanceOf(account).call();
    const symbol = await contract.methods.symbol().call();
    const decimal = await contract.methods.decimals().call();
    const balance = toFixed(amount, decimal);
    return { balance, symbol };
  } catch (e) {
    return "";
  }
}
