import {
  checkMetaMask,
  getAllWorkSpace,
  web3,
  getProposals
} from "@/matalab/witness.js";
import { getStorage, setStorage, voteHistoryKey } from "@/matalab/storage.js";

const app = {
  state: {
    account: "",
    connected: false,
    loadWorkspace: false,
    workspaces: [],
    activeWorkspace: {},
    loadProposals: false,
    activeProposal: {},
    voteHistory: []
  },
  getters: {
    account: state => state.account,
    connected: state => state.connected,
    workspaces: state => state.workspaces,
    loadWorkspace: state => state.loadWorkspace,
    activeWorkspace: state => state.activeWorkspace,
    loadProposals: state => state.loadProposals,
    activeProposal: state => state.activeProposal,
    voteHistory: state => state.voteHistory,
    accountVotes: state => {
      if (state.voteHistory.length > 0) {
        let votes = [];
        for (let record of state.voteHistory) {
          if (record.account === state.account) {
            votes.unshift(record);
          }
        }
        return votes;
      }
      return [];
    }
  },
  mutations: {
    setAccount(state, account) {
      state.account = account;
    },
    setConnected(state, connect) {
      state.connected = connect;
    },
    setWorkspaces(state, spaces) {
      state.workspaces = spaces;
    },
    setLoadWorkspace(state, load) {
      state.loadWorkspace = load;
    },
    setActiveWorkspace(state, workspace) {
      state.activeWorkspace = workspace;
    },
    setLoadProposals(state, load) {
      state.loadProposals = load;
    },
    setActiveProposal(state, proposal) {
      state.activeProposal = proposal;
    },
    setVoteHistory(state, history) {
      state.voteHistory = history;
    }
  },
  actions: {
    getAccount: async ({ commit, state }) => {
      if (state.account === "") {
        const installed = await checkMetaMask();
        if (installed) {
          window.ethereum
            .request({ method: "eth_accounts" })
            .then(accounts => {
              if (accounts[0]) {
                commit("setConnected", true);
                commit("setAccount", accounts[0]);
              }
            })
            .catch(error => console.debug(error));
          window.ethereum.on("disconnect", err => {
            console.debug(err);
            commit("setConnected", false);
            commit("setAccount", "");
          });
          window.ethereum.on("accountsChanged", accounts => {
            if (accounts[0]) {
              commit("setAccount", accounts[0]);
            }
          });
        }
      } else {
        commit("setConnected", true);
      }
    },

    connectWallet: async ({ commit }) => {
      const installed = await checkMetaMask();
      if (installed) {
        window.ethereum
          .request({ method: "eth_requestAccounts" })
          .then(accounts => {
            if (accounts[0]) {
              commit("setConnected", true);
              commit("setAccount", accounts[0]);
            }
          })
          .catch(error => console.debug(error));
      }
    },

    getVoteHistory: ({ commit }) => {
      const data = getStorage(voteHistoryKey);
      let history;
      if (data) {
        history = JSON.parse(data);
      } else {
        history = [];
      }
      commit("setVoteHistory", history);
    },

    saveVoteHistory: ({ commit, state }, record) => {
      record.account = state.account;
      record.date = Date.now();
      for (let i = 0; i < state.voteHistory.length; i++) {
        const h = state.voteHistory[i];
        if (
          h.proposalId === record.proposalId &&
          h.account === record.account
        ) {
          state.voteHistory.splice(i, 1);
        }
      }
      state.voteHistory.push(record);
      const history = state.voteHistory;
      setStorage(voteHistoryKey, JSON.stringify(history));
      commit("setVoteHistory", history);
    },

    getWorkspace: async ({ commit }) => {
      commit("setLoadWorkspace", true);
      const data = await getAllWorkSpace();
      let allWorkSpace = [];
      for (var workspace of data) {
        let data = web3.utils.hexToString(workspace.additionalData);
        let spaceInfo = JSON.parse(data);
        allWorkSpace.push({
          id: workspace.id,
          name: spaceInfo.name,
          spec: spaceInfo.spec,
          contract: workspace["2"]
        });
      }
      commit("setWorkspaces", allWorkSpace);
      commit("setLoadWorkspace", false);
    },

    changeWorkspace: async ({ commit, state, dispatch }, workspace) => {
      if (
        state.activeWorkspace.id &&
        state.activeWorkspace.id === workspace.id
      ) {
        workspace = state.activeWorkspace;
        commit("setActiveWorkspace", workspace);
        commit("setLoadProposals", false);
        return;
      } else {
        workspace.proposals = [];
        commit("setActiveWorkspace", workspace);
        commit("setLoadProposals", true);
      }
      workspace.proposals = [];
      commit("setActiveWorkspace", workspace);
      await Promise.all([
        dispatch("getProposals", workspace),
        dispatch("getWorkspaceName", workspace)
      ]);
      commit("setActiveWorkspace", workspace);
      commit("setLoadProposals", false);
    },

    getProposals: async ({ commit }, workspace) => {
      const proposals = await getProposals(workspace.id >> 20);
      for (var p of proposals) {
        const data = web3.utils.hexToString(p.data);
        let proposalInfo = JSON.parse(data);
        proposalInfo.start = p.start;
        proposalInfo.end = p.end;
        proposalInfo.snapshot = p.snapshot;
        proposalInfo.id = p.id;
        proposalInfo.author = p.author;
        const now = Math.floor(Date.now() / 1000);
        if (Number(p.status) === 0) {
          if (parseInt(p.start) < now && parseInt(p.end) > now) {
            proposalInfo.status = "Active";
          } else if (parseInt(p.start) > now) {
            proposalInfo.status = "Pending";
          } else {
            proposalInfo.status = "Closed";
          }
        } else {
          proposalInfo.status = "Closed";
        }

        workspace.proposals.push(proposalInfo);
      }
      commit("setActiveWorkspace", workspace);
    },

    getWorkspaceName: async ({ state, dispatch }, workspace) => {
      if (!workspace.name) {
        if (state.workspaces.length === 0) {
          await dispatch("getWorkspace");
          for (let w of state.workspaces) {
            if (w.id === workspace.id) {
              workspace.name = w.name;
              workspace.contract = w.contract;
              break;
            }
          }
        }
      }
    },

    changeProposal: async ({ commit, dispatch, state }, info) => {
      if (info.proposal) {
        commit("setActiveProposal", info.proposal);
      } else {
        const workspaceId = (info.id >> 20) << 20;
        await dispatch("changeWorkspace", { id: workspaceId.toString() });
        const workspace = state.activeWorkspace;
        if (workspace.proposals.length > 0) {
          for (let p of workspace.proposals) {
            if (p.id === info.id) {
              commit("setActiveProposal", p);
              break;
            }
          }
        }
      }
    },

    updateProposal: async ({ commit, state }, result) => {
      const workspace = state.activeWorkspace;
      if (workspace.proposals.length > 0) {
        for (let i = 0; i < workspace.proposals.length; i++) {
          if (workspace.proposals[i].id === result.id) {
            workspace.proposals[i].result = result.voteResult;
            commit("setActiveWorkspace", workspace);
            return;
          }
        }
      }
    }
  }
};

export default app;
